=<template>
    <nav class="level navbar has-shadow is-marginless">
        <div class="level-left">
            <a class="button is-white level-item" @click="$emit('toggleSidebar')">
                <span class="icon is-small">
                    <span class="lnr" :class="isSidebarOpen ? 'lnr-cross' : 'lnr-menu'"></span>
                </span>
            </a>
            <a class="button is-white level-item">
                <span class="icon is-small">
                    <span class="lnr lnr-home"></span>
                </span>
            </a>
            <a class="button is-white level-item">My Tasks</a>
            <b-dropdown hoverable>
                <button class="button is-info" slot="trigger">
                    <span class="icon">
                        <span class="lnr lnr-plus-circle"></span>
                    </span>
                </button>

                <b-dropdown-item>New Project</b-dropdown-item>
                <b-dropdown-item>New Tasks</b-dropdown-item>
                <b-dropdown-item>New Discussion</b-dropdown-item>
                <b-dropdown-item>New Message</b-dropdown-item>
            </b-dropdown>            
        </div>
        <div class="level-item">
            <div class="control has-icons-left search">
                <input class="input is-rounded is-small" type="search" placeholder="Search...">
                <span class="icon is-left">
                    <i class="lnr lnr-magnifier"></i>
                </span>
            </div>
        </div>
        <div class="level-right">
            <b-dropdown hoverable position="is-bottom-left">
                <button class="button is-white" slot="trigger">
                    <span>{{name}}</span>
                    <figure class="image is-32x32 avatar">
                        <img class="is-rounded" :src=avatar>
                    </figure>
                </button>

                <b-dropdown-item>My Profile Settings</b-dropdown-item>
                <hr class="dropdown-divider">
                <b-dropdown-item>Log Out</b-dropdown-item>
            </b-dropdown>
        </div>
    </nav>
</template>

<script>
export default {
  name: 'navbar',
  
  props: ['isSidebarOpen'],

  data () {
    return {
      name: faker.name.findName(),
      avatar: faker.internet.avatar()
    }
  }
}
</script>


<style scoped>
.level-item {
    margin: 0 20px;
}
.search {
    width: 400px
}
</style>
      
