<template>
  <div class="columns is-centered">
    <div class="column" :class="{'is-two-thirds': !detailed, 'is-two-fifths': detailed}">
      <div class="box z-depth-1">
        <button id="btnTaskNew" class="button is-link is-outlined">New Task</button>
        <div class="panel">
          <task-item v-for="(task, index) in taskList" :key="index" v-model='taskList[index]' @handleDetail='handleDetail' />
        </div>
      </div>
    </div>
    <div v-if="detailed" class="column">
      <div class="box z-depth-1">
        <task-detail :task='detailed' />
      </div>
    </div>
  </div>
</template>

<script>
import TaskItem from '@/components/TaskItem'
import TaskDetail from '@/components/TaskDetail'

export default {
  components: {
    TaskItem, TaskDetail
  },

  data () {
    return {
      detailed: null,
      tasks: [
        {name: 'Collect knowledge document', done: false, active: false, duedate: faker.date.future()},
        {name: 'Compile proposal', done: false, active: false, duedate: faker.date.future()},
        {name: 'Setup meetings', done: false, active: false, duedate: faker.date.future()},
        {name: 'Buy more coffee', done: false, active: false, duedate: faker.date.future()},
        {name: 'Create prerequisites', done: false, active: false, duedate: faker.date.future()}
      ]
    }
  },

  methods: {
    handleDetail: function (task) {
      this.tasks.forEach( task => {
        if (task) {
          task.active = false
        }
      })
      const idx = this.tasks.findIndex(t => t.name === task.name)
      this.tasks[idx].active = !this.tasks[idx].active
      this.detailed = this.tasks[idx]
    }
  },

  computed: {
    taskList: function () {
      let result = this.tasks
      while (result.length < 12) {
        result.push('')
      }
      return result
    }
  }
}
</script>


<style lang="scss" scoped>
#btnTaskNew {
  margin-bottom: 2rem;
}
</style>
