<template>
  <a class="panel-block" 
    :class="{'is-active': task.active, 'has-text-link': task.active}" 
    @click="handleDetail()">
    <div class="level">
      <div class="level-left">
        <div class="level-item">
          <span class="icon is-medium">
            <i class="lnr lnr-checkmark-circle" 
              v-if="task"
              :class="{
                'has-text-primary': task.done, 
                'has-text-grey-lighter': !task.done,
                'is-size-4': !over,
                'is-size-3': over
              }" 
              @click="handleDone()"
              @mouseover="over = true"
              @mouseout="over = false"/>
          </span>
        </div>
        <div class="level-item">
          {{task.name}}
        </div>
      </div>
      <div class="level-right">

      </div>
    </div>
  </a>
</template>

<script>
export default {
  props: ['value'],
  
  data () {
    return {
      task: this.value,
      over: false
    }
  },

  methods: {
    handleDetail: function () {
      this.$emit('handleDetail', this.task);
    },
    handleDone: function () {
      this.task.done = !this.task.done
      this.$emit('input', this.task);
      this.$snackbar.open({
        duration: 5000,
        message: `Task ${this.task.name} is marked as ${this.task.done ? 'done' : 'not done'}`,
        queue: false,
        type: 'is-danger',
        actionText: 'UNDO',
        onAction: () => {
          this.task.done = !this.task.done
          this.$emit('input', this.task);
        }
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.panel-block.is-active {
  border-left-width: 5px;
}
</style>
